.ticket_pg{
    background: #fff;
    border-radius: 5px;
    height: 96vh;
    /* margin: auto; */
    margin: 36px 33px 0 30px;
    margin-top: 54px;
}
.ticket_title{
    display: flex;
    justify-content: space-between;
    padding: 32px;
    align-items: center;
}
.titleR,.titleR_icon{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.titleR_icon p{
    margin-left: 8px;
    color: #4B506D;
}
.icon{
    color: #C5C7CD;
}
.filter{
    margin-left: 32px;
}
