.tab_info{
    display: grid;
    grid-template-columns: 3fr 2fr 2fr 1fr;
    margin: auto;
    padding: 24px 31px;
    border-bottom: 1px solid #dfe0e5;
    align-items: center;
}
.tab_info:hover{
    background: #F7F8FC;
}
.info_title{
    display: grid;
    grid-template-columns: 1fr 7fr;
}
.info_title img{
    width: 54px;
    height: 54px;
    border: 5px solid #dfe0e5;
    border-radius: 50%;
}
.ticket_tab{
    margin: auto;
}
.info_height h4{
    margin-bottom: 4px;
}
.info_status{
    display: flex;
    /* grid-template-columns: 1fr 1fr; */
    justify-content: space-between;
    align-items: center;
}
.info_status button{
    padding: 5px 20px;
    border-radius: 10px 10px 10px 10px;
    background: red;
    color: #fff;
    border: none;
}
.info_status img{
    margin-right: 41px;
    margin-left: 82px;
}
